export default {
	data() {
	    return {
	        scrollData: {},
			//购物车缓存 Storage 名称
			shoppingCartStorageName: 'shopping_cart',
			//门店信息
			storeData:{},
			//购物车
			shoppCart:[],
			//头部选项卡 参数为 0不显示选项卡,1,2
			tabIndex:1,
			//收获地址
			addressData: null,
			//手机隐私
			ysStatus: true,
			//优惠券
			couponData: [{
				id:"ASD2020410003",
				icon: "icon-ticket_money_fill",
				//标题
				tit: "门店新客立减",
				//说明
				content: "15元商家券",
				//备注
				remark: "满减券和商品折扣可同享",
				//面额
				value: 15
			},{
				id:"ASD2020410001",
				icon: "icon-ticket_money_fill",
				tit: "商家抵用立减",
				content: "-￥50",
				//面额
				value: 50
			}],
			//结算价
			totalPrice: 0,
			//包装费
			totalPacking:0,
			//已优惠价
			totalYhPrice: 0,
			//商品数量
			totalNumber: 0,
			remark: ""
	    }
	},
	onLoad(option){
		let that = this
		let totalPrice = 0
		let totalPacking=0
		let totalNumber = 0
		let totalOldPrice = 0
		if(option.sid){
			let carts = uni.getStorageSync(that.shoppingCartStorageName) || []
			// console.log("+++++++++",JSON.stringify(carts))
			if(carts){
				//根据缓存数据 获取购物车中属于本商店的商品
				for(let i in carts){
					if(carts[i].store_id == option.sid){
						that.shoppCart = carts[i].shopping_cart;
						that.storeData = carts[i]
						break;
					}
				}
			}
			let cj = 0
			let op = 0
			let p = 0
			let pk = 0
			let num = 0
			//计算总商品数量和总价
			for(var i in that.shoppCart){
				p = Number(that.shoppCart[i].price);
				pk = Number(that.shoppCart[i].packing);
				op = Number(that.shoppCart[i].oldprice);
				num = Number(that.shoppCart[i].number)
				totalNumber += num
				totalPrice += p * num
				totalPacking += pk * num
				if(op > p){
					cj +=  (op - p) * num
				}
			}
			that.totalPrice = totalPrice + totalPacking
			that.totalYhPrice = cj
		}
		
	},
	onPageScroll(e){
	    this.scrollData = e;
	},
	methods: {
		privacyStatusChange(e){
			this.ysStatus = e.target.value
		},
		
		//提交订单
		submit(){
			let that = this
			let goodsID = []
			for (var i in that.shoppCart){
				goodsID.push(that.shoppCart[i].id)
			}
			let data = {
				//商品id
				goods : goodsID,
				//使用的优惠券 id
				coupon: [],
				//手机隐私
				privacy_status: that.ysStatus ? 1 : 0
			}
			
			//向后台发送 data 进行真实合计，生成订单，并返回订单信息
			//模拟请求数据
			uni.showLoading({
			    title: '正在生成订单'
			});
			setTimeout(()=>{
				uni.hideLoading()
			},1000)
			//假设返回了订单信息，跳转至支付页面
			let orderId="DGSD2020041035"
			uni.redirectTo({
			    url: '/pages/order/pay?id=' + orderId +"&payMoney=" + that.totalPrice
			});
		}
	}
}
